<template>
  <div id="loadsave">
    <label class="button">
      <input type="file" name="file" @change="loadWorld">
      Load World
    </label>
    <div class="button" @click="saveWorld">
      Save Copy of World
    </div>
    <div>
      <h3 v-if="world">{{world.title}}</h3>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LevelLoadSave',
  created () {
    var vm = this;
    document.bus.$on("world-loaded", (world) => {
      vm.world = world;
    });
  },
  data () {
    return {
      world: undefined,
      loadWorld (el) {
        var world = {};
        
        var input = el.target;
        var file = input.files[0];
        var reader = new FileReader();

        if (!file) return;

        reader.onload = function(ev) {
          var parse = new DOMParser();
          var dom = parse.parseFromString(ev.target.result, "text/xml");
          
          world.dom = dom;
          world.things = [];

          // Process all the things
          var things = dom.querySelectorAll("ThingSaveData");
          
          things.forEach(function(thing) { 
            var obj = {
              dom: thing
            }

            obj.refresh = function() {
              obj.type = obj.dom.querySelector("PrefabName").innerHTML;

              var pos = obj.dom.querySelector("WorldPosition");
              obj.pos = { 
                x: pos.querySelector("x").innerHTML * 1, 
                y: pos.querySelector("y").innerHTML * 1, 
                z: pos.querySelector("z").innerHTML * 1
              };
            }
            obj.refresh();

            world.things.push(obj);
          });

          world.title = things.length + " loaded things.";


          document.bus.$emit("world-loaded", world);

          input.value = "";
        }

        reader.readAsText(file);
      },
      saveWorld () {
          var ser = new XMLSerializer();
          // TODO: Change this to download a file
          //document.querySelector("#out").value = ser.serializeToString(dom);
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#loadsave {
  margin: 20px;
}

input[type=file] {
  display: none;
}

.button {
  display: inline-block;
  padding: 5px 10px;

  background-color: #e77;
  border: 3px solid #c11;

  color: white;
  font-weight: bolder;
  font-size: 16pt;
  
  user-select: none;
}
</style>



